<template>
  <div class = "container">
    <h1>Full Stack Help Requests</h1>
    <div class="create-post">
      <label for="create-post">Issue Submission:</label>
    </div>
    <input type="text" id="create-post" v-model="text" placeholder = "I'm having trouble with...">
    <v-btn class="submit" v-on:click = "createPost">Submit</v-btn>
    <hr>
    <p class = "error" v-if="error">{{ error }}</p>
    <div class = "posts-container">
      
      <div class = "post"
        v-for="(post,index) in posts" 
        v-bind:item="post" 
        v-bind:index = "index" 
        v-bind:key="post._id" 
        v-on:dblclick="deletePost(post._id)"
        >
        <v-card class = "card d-flex .justify-center">
        <v-card-title>
          <v-toolbar-title class = "name">{{post.name}}</v-toolbar-title>
        </v-card-title>
        <v-card-text>
          <p class = "text">{{ post.text }}</p>
          <p class = "text">{{ `${post.createdAt.getMonth()}/${post.createdAt.getDate()}/${post.createdAt.getFullYear()}` }}</p>
        </v-card-text>
        </v-card>
        
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService';

export default {
  name: 'PostComponent',
  data() {
    return{
      posts: [],
      error: '',
      text: '',

    }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPost(){
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    }
  }
}
</script>

